            model.name = key
            model.scale.set(.075,.075,.075)
            model.position.set(0,-.6, 0)

            model.traverse((child) => {
                if (child.isMesh){
                    let toonMaterial = new THREE.MeshToonMaterial({ color : 0xFFC332, gradientMap : tex});
                    child.material = shaderMaterial;
                    child.castShadow = true;
                }
            });

            models[key] = model
